.header__container {
    border-bottom: 1px solid rgba(54, 54, 54, 0.7);
    max-width: 577px;
    margin: 0 auto;
    padding: 0 28px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

@media screen and (min-width: 1000px) {
    .header__container {
        max-width: 830px;
    }
}